* {
   margin: 0;
   padding: 0;
} 

body {
   /*background-color: #d83;*/
   font: 14px Arial;
}

::-webkit-scrollbar {
   display: none;
}

::-moz-scrollbar {
   display: none;
}

header {
   /*background-color: #abe;*/
   display: block;

   width: 100%;
   padding-top: 30px;

   /*border-bottom: 2px solid #9cc;*/

   height: 30px;
   position: sticky;
   top: -100px;
   z-index: 100;

   text-align: center;
   text-transform: capitalize;
   letter-spacing: 3px;

   nav {
      width: 100%;
      padding: 10px 0 30px 0;
      background: linear-gradient(to bottom, white, rgba(255, 255, 255, 0));
   }
}

.title {
   display: block;
   background-image: url(../img/tit.png);
   background-size: contain;
   background-repeat: no-repeat;
   margin: auto;
   width: 250px;
   height: 70px;

}

.index-title {
   animation-name: title;
   animation: 3s ease 0s normal forwards 1 title;
}

.nav-list {
   display: flex;
   flex-flow: row nowrap;
   justify-content: center;
   align-items: flex-start;
   align-content: center;
   width: auto;
   margin: auto;
}

.nav-items {
   list-style: none;
   padding: 0 16px;

   img {
      flex-basis: 30%;
      border-radius: 5px;
      max-width: 100%;
      image-resolution: snap;

      -webkit-transition: all 200ms ease;
      -moz-transition: all 200ms ease;
      -o-transition: all 200ms ease;
      transition: all 200ms ease;
   }
}

.nav-home { width: 42px; }

.nav-gallery, .nav-contact {
   width: 63px;
}

.nav-gallery:hover , .nav-contact:hover {
width: 66px;
}

.nav-home:hover {
width: 45px;
}

.wrapper {
   /*background-color: #eab;*/
   display: block;
   position: relative;
   margin: 10vh auto;
   width: 70%;
   max-width: 1300px;

   animation-name: wrapper;
   animation: 1166ms ease 0s normal forwards 1 wrapper;
}

.picture {
   display: flex;
   flex-flow: row nowrap;
   justify-content: center;
   width: auto;
   height: 600px;
   margin: 0 10%;
}

.felicia {
   flex-basis: 1 1 auto;
   background-image: url(../img/felicia.jpg);
   background-position: center;
   background-size: cover;
   background-repeat: no-repeat;
   border: 2px solid black;
   border-radius: 10px;
   width: 100%;
   height: 100%;
}

.text { 
   padding: 50px 10%;
}

.text h1 {
   padding-bottom: 10px
}

.text p {
   font: 15px Arial;
   padding: 10px;
   width: 100%;
   word-spacing: auto;
}

